.App {
  height: 100vh;
}
.arrow {
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;

  position: absolute;
}

.arrow-right {
  border-left: 15px solid #000;
  right: 0;
}

.arrow-left {
  border-right: 15px solid #000;
  left: 0;
}

.container {
  margin: 15% auto;
  font-size: 24px;
  position: relative;
  width: 15em;
}

.header {
  text-align: center;
}

.modal-dialog {
  margin: 0 !important;
}

.modal-content {
  margin: 50vh auto auto 13vw;
}

.Settings {
  width: 15vw;
  min-width: 300px;
  position: fixed;
  top: 6vh;
  right: 0.5vw;
  background-color: #f5f6f8;
}
@media screen and (max-width: 700px) {
  .Settings {
    margin: 0 auto;
    align-self: center;
  }
}

.Header {
  background-color: #ff8000;
  height: 5vh;
}

.Header .title {
  color: #f5f6f8;
  font-family: "Roboto", sans-serif;
  font-size: 1.7rem;
}

.Header .title-nav {
  margin: 0 auto;
}

/* Scroll hidder */
* {
  -ms-overflow-style: none;
}

*::-webkit-scrollbar {
  display: none;
}

.Stats .Rightside {
  padding-top: 70px;
}

#chartdiv {
  width: 100vw;
  height: 95vh;
}

.ProjectCard {
  width: "22vw";
}

.ProjectCard .projectImg {
  height: 11vw;
  width: 22vw;
}
